<script>
import StopWatch from '@/components/stop-watch';
import Steps from '@/components/steps';
import { API_BASE_PATH } from '@/store/consts';
export default {
  name: 'review',
  components: {
    StopWatch,
    Steps
  },
  data() {
    return {
      error: '',
      barcodeNumber: '',
      isProcessing: false
    };
  },
  created() {
    this.$store.dispatch('scrollTop');
  },
  methods: {
    buyTicket() {
      if (this.giftCardRedirectUrl) {
        window.location = this.giftCardRedirectUrl + '?order=' + this.giftCardOrder;
      }
      this.isProcessing = true;
      this.$store
        .dispatch('ticketing/buyTicket', this.selectedMovie)
        .then(res => {
          try {
            // const {Code} = res.Response;
            const { Code, ErrorText, Packet } = res.body.Response;
            if (Code === '-1' && Packet.Response.Code === '0') {
              this.error = '';
              const barcodeList = Packet.Response.Pickups.Pickup.BarCodes.BarCode;
              const upcBarcodeObj = barcodeList.find(barcode => barcode.CodeType === 'UPC');
              this.barcodeNumber = upcBarcodeObj.BarCodeData;
              this.$store.dispatch('ticketing/stopCountDown');
              this.$store.dispatch('ticketBought');
              const giftCardNumber = this.$store.getters['ticketing/giftCardNumber'];
              if (giftCardNumber) {
                const holdId = this.$store.getters['ticketing/holdId'];
                const selectedMovie = this.$$store.getters['movies/selectedMovie'];
                const ticketDetail = this.$store.getters['ticketing/ticketDetail'];
                const totalPrice = this.$store.getters['ticketing/totalTicketsPrice'];
                window.dataLayer.push({ ecommerce: null }); // Clear the previous ecommerce object.
                window.dataLayer.push({
                  event: 'giftCardPurchase',
                  ecommerce: {
                    purchase: {
                      actionField: {
                        id: holdId,
                        revenue: totalPrice, // Total transaction value (incl. tax and shipping)
                        tax: ticketDetail.tax, // Service charge = 1.5 per ticket
                        coupon: giftCardNumber // gift card number if applicable otherwise leave empty
                      },
                      products: [
                        {
                          name: selectedMovie.Title,
                          id: selectedMovie.Slug, // Transform the Movie Title to it's slug version => triblend-android-t-shirt
                          price: selectedMovie.price, // Price = Ticket price * Quantity'
                          variant: ticketDetail.ticketType, // Ticket Type Name
                          quantity: ticketDetail.quantity
                        }
                      ]
                    }
                  }
                });
              }
            } else {
              this.error = ErrorText || 'Your order is failed';
            }
          } catch (err) {
            this.error = err;
          }
        })
        .catch(err => {
          this.error = err;
        })
        .finally(() => {
          this.isProcessing = false;
        });
    }
  },
  computed: {
    imageUrl() {
      return this.$store.getters['movies/movieImage'];
    },
    barcodeImage() {
      return `${API_BASE_PATH}/api/get-barcode?refID=${this.barcodeNumber}`;
    },
    selectedMovie() {
      return this.$store.getters['movies/selectedMovie'];
    },
    giftCardRedirectUrl() {
      return this.$store.getters['ticketing/giftCardRedirectUrl'];
    },
    giftCardOrder() {
      return this.$store.getters['ticketing/giftCardOrder'];
    },
    ticketDetail() {
      return this.$store.getters['ticketing/ticketDetail'];
    },
    plainDateSelected() {
      return this.$store.getters['ticketing/plainDateSelected'];
    },
    totalTicketsPrice() {
      return this.$store.getters['ticketing/totalTicketsPrice'];
    },
    totalTicketCount() {
      return this.$store.getters['ticketing/totalTicketCount'];
    },
    totalServicePrice() {
      return this.$store.getters['ticketing/totalServicePrice'];
    },
    totalPrice() {
      return this.$store.getters['ticketing/totalPrice'];
    },
    paymentData() {
      return this.$store.getters['ticketing/paymentData'];
    },
    selectedSeats() {
      return this.$store.getters['ticketing/selectedSeats'];
    },
    logID() {
      return this.$store.getters['ticketing/logID'];
    },
    maxUsingGiftCard() {
      return this.$store.getters['ticketing/giftCardMaxUsage'].toFixed(2);
    }
  }
};
</script>

