<script>
import Breadcrumb from '@/components/breadcrumb';
import MovieInfo from '@/components/movie-info';
import moment from 'moment';
import Vue from 'vue';
import Datetime from 'vue-datetime';
import 'vue-datetime/dist/vue-datetime.css';
import VueSilentbox from 'vue-silentbox';

export let movieDetail = [];
Vue.use(VueSilentbox);
Vue.use(Datetime);

export default {
  name: 'Movie',
  components: { MovieInfo, Breadcrumb },
  data() {
    const selectedShow = this.$store.getters['ticketing/selectedShow'] || undefined;
    const avDates = this.$store.getters['movies/availableDays'];
    return {
      defaultInfo: [
        {
          label: 'Matinee',
          description: ['Monday-Thursday $10', 'Friday-Sunday $11', 'all shows starting at 11 AM.']
        },
        {
          label: 'Early Riser',
          description: [
            'Early Riser Monday-Thursday, including Tuesday $6.50 all shows before 11 AM.',
            'Tuesday $8 all day (early riser still applies).'
          ]
        },
        { label: 'Matinee', description: ['S-Thurs $10 & Fri-Sat $10 after 4 pm'] }
      ],
      formIsValidate: false,
      dayActiveIndex: undefined,
      selectedDate: undefined,
      timeActiveIndex: undefined,
      selectedShow,
      collapseVisible: false,
      isLoading: false
    };
  },
  computed: {
    infoHTML() {
      return this.selectedMovie.Info || '';
    },
    ticketInfo() {},
    timeHasSelected() {
      // debugger;
      console.log({ timeActiveIndex: this.timeActiveIndex });
      return this.timeActiveIndex >= 0;
    },
    imageUrl() {
      return this.$store.getters['movies/movieImage'];
    },
    selectedMovie() {
      return this.$store.getters['movies/selectedMovie'];
    },
    availableDateRange() {
      return this.$store.getters['movies/availableDateRange'];
    },
    filteredAvailableTime() {
      const availableTime = this.$store.getters['movies/availableTimes'];
      const day = moment(this.selectedDate).format('YYYY-MM-DD');
      return availableTime.filter(item => item.key === day);
    },
    availableDays() {
      return this.$store.getters['movies/availableDays'];
    },
    priceGroups() {
      return this.$store.getters['ticketing/priceGroups'];
    }
  },
  created() {
    this.$store.dispatch('scrollTop');
    this.load();
  },
  methods: {
    load() {
      try {
        const routeTitle = this.$route.params.title;
        const { dayActiveIndex, selectedDate, timeActiveIndex, selectedShow, title } =
          JSON.parse(localStorage.getItem('booking')) || {};
        // debugger;
        console.log({ timeActiveIndex22: timeActiveIndex });
        if (title !== routeTitle) {
          localStorage.setItem('booking', '');
          return;
        }
        this.selectedShow = selectedShow;
        this.timeActiveIndex = timeActiveIndex;
        this.selectedDate = selectedDate;
        this.dayActiveIndex = dayActiveIndex;
        // setTimeout(() => {
        //   this.selectedDate = selectedDate;

        // });
      } catch (e) {}
      if (!this.selectedDate) {
        // this.setActive(this.availableDays[0], 0);
        // setTimeout(() => {
        //   if (this.availableDays.length) {
        //     this.setActive(this.availableDays[0], 0);
        //   }
        // }, 200);
      }
    },
    save() {
      const title = this.$route.params.title;
      const { dayActiveIndex, selectedDate, timeActiveIndex, selectedShow } = this;

      localStorage.setItem(
        'booking',
        JSON.stringify({ dayActiveIndex, selectedDate, timeActiveIndex, selectedShow, title })
      );
    },
    selectedShowItem() {
      return this.$store.getters['ticketing/selectedShow'];
    },
    setActive(day, index) {
      this.dayActiveIndex = index;
      this.selectedDate = day.val;

      if (this.filteredAvailableTime.length) {
        this.timeSelect(this.filteredAvailableTime[0], 0);
      }
      // this.timeActiveIndex = 0
    },
    timeSelect(time, index) {
      this.timeActiveIndex = index;
      if (this.selectedShow && time.value.ID !== this.selectedShow.ID) {
        this.$store.dispatch('ticketing/changedPerformanceId');
      }
      this.selectedShow = time.value;
    },
    bookTicket() {
      if (!this.selectedShow) return;
      this.$store.dispatch('ticketing/setSelectedShow', this.selectedShow);
      this.isLoading = true;
      this.$store.dispatch('ticketing/loadTicket').then(() => {
        this.save();
        this.isLoading = false;
        // console.log(this.priceGroups);
        if (this.priceGroups.length > 1) {
          this.$router.push({ name: 'seats', params: { title: this.$route.params.title } });
        } else {
          this.$router.push({ name: 'tickets', params: { title: this.$route.params.title } });
        }
      });
    }
  },
  beforeDestroy() {
    this.save();
  },
  watch: {
    selectedDate: function(selected) {
      if (selected && this.availableDays.length) {
        const key = moment(selected).format('YYYY-MM-DD');
        this.dayActiveIndex = this.availableDays.findIndex(ad => ad.key === key);

        if (this.filteredAvailableTime.length) {
          this.timeSelect(this.filteredAvailableTime[0], 0);
        }
      }
    },
    selectedMovie() {
      this.load();
    }
  }
};
</script>

