<script>
export default {
  name: 'steps',
  props: {
    activeStep: String
  },
  computed: {
    selectedShow() {
      return this.$store.getters['ticketing/selectedShow'];
    },
    priceGroups() {
      return this.$store.getters['ticketing/priceGroups'];
    },
    isTicketSelected() {
      try {
        return this.$store.getters['ticketing/totalTicketCount'];
      } catch (e) {
        return false;
      }
    },
    seatsHasSelected() {
      return this.$store.getters['ticketing/holdId'];
    },
    paymentData() {
      return this.$store.getters['ticketing/paymentData'];
    },
    lastStep() {
      return this.steps[this.steps.length - 1];
    },
    list() {
      return this.steps.slice(0, this.steps.length - 1);
    },
    hasLogged() {
      return this.$store.getters['ticketing/logID'];
    },
    getSteps() {
      const ticketStep = {
        routerLinkCondition: true,
        name: 'tickets',
        title: 'Select Tickets'
      };
      const otherSteps = [
        {
          routerLinkCondition: this.isTicketSelected,
          name: 'seats',
          title: 'Select Seats'
        },
        {
          routerLinkCondition: this.isTicketSelected && this.seatsHasSelected,
          name: 'payment',
          title: 'Enter Payment'
        },
        {
          routerLinkCondition: this.hasLogged,
          name: 'review',
          title: 'Review Order'
        }
      ];

      if (this.priceGroups.length > 1) {
        return otherSteps;
      }

      return [ticketStep, ...otherSteps];
    }
  }
};
