<template>
  <div v-if="hasStarted" class="c-info__time" :class="{'is-ending' : isEnding}">
    <div class="c-info__time__text">
      <svg width="20" height="24" fill="none" xmlns="http://www.w3.org/2000/svg">
        <path d="M10 4a10 10 0 1 0 0 20 10 10 0 0 0 0-20zm5 11H9V9h2v4h4v2zM14 0H6v2h8V0z" fill="#444444" /></svg>
      Time left to order</div>
    <div class="c-info__time__counter">
      <div id="timer">
        <span id="minutes">{{ minutes }}</span>
        <span id="middle">:</span>
        <span id="seconds">{{ seconds }}</span>
      </div>
    </div>
  </div>

</template>

<script>
  export default {
    name: "stop-watch",
    methods: {
      padTime: function(time) {
        return (time < 10 ? '0' : '') + time;
      },
    },
    computed: {
      hasStarted: function() {
        return this.countDown > 0;
      },
      countDown: function() {
        return this.$store.getters['ticketing/countdown'];
      },
      minutes: function() {
        const minute = Math.floor(this.countDown/60);
        return this.padTime(minute);
      },
      seconds: function() {
        const seconds = this.countDown % 60;
        return this.padTime(seconds);
      },
      isEnding: function() {
        return this.countDown < 59;
      }
    },
  }
</script>
