<template>
  <div id="main" aria-label="content">
    <header></header>
    <main>
      <section class="section section--steps">
        <div class="section__inner-wrapper">
          <steps :activeStep="'tickets'"></steps>
        </div>
      </section>
      <section class="section section--showtimes">
        <div class="section__inner-wrapper">
          <div v-if="isLoading" class="c-loading">
            <svg
              width="100"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 100 100"
              preserveAspectRatio="xMidYMid"
              class="lds-dual-ring"
              style="background:0 0"
            >
              <circle
                cx="50"
                cy="50"
                fill="none"
                stroke-linecap="round"
                r="40"
                stroke-width="4"
                stroke="#363472"
                stroke-dasharray="62.83185307179586 62.83185307179586"
                transform="rotate(162.457 50 50)"
              >
                <animateTransform
                  attributeName="transform"
                  type="rotate"
                  calcMode="linear"
                  values="0 50 50;360 50 50"
                  keyTimes="0;1"
                  dur="1s"
                  begin="0s"
                  repeatCount="indefinite"
                />
              </circle>
            </svg>
          </div>
          <div v-else-if="ticketDetail && selectedMovie" class="c-showtimes c-showtimes--tickets">
            <div class="c-showtimes__col">
              <div class="c-showtimes__steps">
                <div class="c-showtimes__steps__col c-showtimes__steps__movies">
                  <div class="c-showtimes__movies__cover">
                    <img v-lazy="imageUrl" alt="" class="c-showtimes__movies__image" />
                  </div>
                  <div class="c-showtimes__movies__info">
                    <h3 class="c-showtimes__movies__title">
                      {{ selectedMovie.Title | titleCase }}
                    </h3>
                    <div class="c-showtimes__movies__details u-flex">
                      <p class="c-showtimes__details">{{ plainDateSelected }}</p>
                      <p class="c-showtimes__details">{{ ticketDetail.cinemaName }}</p>
                      <p class="c-showtimes__details">{{ ticketDetail.salonName }}</p>
                    </div>
                  </div>
                </div>
                <div class="c-showtimes__steps__col">
                  <div class="c-ticket">
                    <div v-for="ticketType in ticketTypes" class="c-ticket__items">
                      <div class="c-ticket__items__col c-ticket__items--type">
                        {{ ticketType.Name }}
                      </div>
                      <div class="c-ticket__items__col c-ticket__items--price">
                        ${{ ticketType.Price }} ea
                      </div>
                      <div class="c-ticket__items__col c-ticket__items--addremove">
                        <div
                          class="c-addremove__col c-addremove--remove"
                          @click="decrement(ticketType.Name)"
                        >
                          <svg width="12" height="4" fill="none" xmlns="http://www.w3.org/2000/svg">
                            <path d="M0 2h12" stroke="#444" stroke-width="3" />
                          </svg>
                        </div>
                        <div class="c-addremove__col c-addremove--count">
                          <span>{{ ticketTypeCount[ticketType.Name] || 0 }}</span>
                        </div>
                        <div
                          class="c-addremove__col c-addremove--add"
                          @click="increment(ticketType.Name)"
                        >
                          <svg
                            width="12"
                            height="12"
                            fill="none"
                            xmlns="http://www.w3.org/2000/svg"
                          >
                            <path d="M0 6h12M6 0v12" stroke="#fff" stroke-width="3" />
                          </svg>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div class="c-showtimes__col">
              <div class="c-info c-info--summary">
                <div class="c-info__alert">
                  <svg width="14" height="14" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path
                      d="M7.7 4.9H6.3V3.5h1.4v1.4zm0 5.6H6.3V6.3h1.4v4.2zM7 0a7 7 0 1 0 0 14A7 7 0 0 0 7 0z"
                      fill="#F1751B"
                    ></path>
                  </svg>
                  <p class="c-info__alert__text">
                    Seat Selection, Age restricted. There will be a ${{
                      ticketDetail.ServiceCharge.toFixed(2)
                    }}
                    service charge per ticket.
                  </p>
                </div>
                <div class="c-info__summary">
                  <h3 class="c-info__title c-info--title-summary">purchase summary</h3>
                  <ul class="c-info__summary__list">
                    <li class="c-info__summary__list__items">
                      <div class="c-info__summary__text">{{ this.totalCount }} Ticket</div>
                      <div class="c-info__summary__text">${{ formattedTotalTicketsPrice }}</div>
                    </li>
                    <li class="c-info__summary__list__items">
                      <div class="c-info__summary__text">Service Charge</div>
                      <div class="c-info__summary__text">${{ formattedTotalServicePrice }}</div>
                    </li>
                    <li v-if="ticketDetail.tax" class="c-info__summary__list__items">
                      <div class="c-info__summary__text">
                        Tax
                      </div>
                      <div class="c-info__summary__text">${{ formattedTotalTax }}</div>
                    </li>
                  </ul>
                  <div class="c-info__summary__total">
                    <div class="c-info__summary__text">Total:</div>
                    <div class="c-info__summary__text">${{ totalPrice }}</div>
                  </div>
                  <div class="c-info__summary__btn">
                    <button
                      class="c-btn c-btn--summary"
                      :class="{ 'is-active': formIsValidate }"
                      @click="submit"
                    >
                      Next: Select Seats
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div v-else-if="error" class="error">
            <div class="c-message__info has-error">
              <svg
                width="18"
                height="18"
                viewBox="0 0 18 18"
                fill="none"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path
                  d="M9 0a9 9 0 109 9 9.026 9.026 0 00-9-9zm3.713 11.652l-1.061 1.06L9 10.06l-2.652 2.652-1.06-1.06L7.938 9 5.287 6.348l1.061-1.06L9 7.938l2.652-2.652 1.06 1.061L10.06 9l2.652 2.652z"
                  fill="#DF2137"
                />
              </svg>
              <span>Error:</span> {{ error }}
            </div>
          </div>
          <stop-watch></stop-watch>
        </div>
      </section>
    </main>
    <footer></footer>
  </div>
</template>

<script>
import StopWatch from '@/components/stop-watch';
import Steps from '@/components/steps';

export default {
  name: 'Tickets',
  props: {
    title: String
  },
  components: {
    Steps,
    StopWatch
  },
  data() {
    // const ttc = this.$store.getters['ticketing/ticketTypeCount'];
    // const { ...ticketTypeCount } = (ttc || {});
    return {
      error: '',
      ticketTypeCount: {},
      isLoading: false
      // formIsValidate: false
    };
  },
  methods: {
    increment(type) {
      if (this.ticketTypeCount[type] === undefined) {
        this.ticketTypeCount[type] = 0;
      }
      if (this.totalCount < 10) {
        this.ticketTypeCount[type]++;
        this.ticketTypeCount = { ...this.ticketTypeCount };
      }
    },
    decrement(type) {
      if (this.ticketTypeCount[type] === undefined) {
        this.ticketTypeCount[type] = 0;
      }
      if (this.totalCount > 0 && this.ticketTypeCount[type] > 0) {
        this.ticketTypeCount[type]--;
        this.ticketTypeCount = { ...this.ticketTypeCount };
      }
    },
    submit() {
      if (!this.formIsValidate) return;
      this.$store.dispatch('ticketing/setTicketCount', {
        ticketTypeCount: this.ticketTypeCount
      });
      this.$router.push({ name: 'seats', params: { title: this.selectedMovie.Slug } });
    }
  },
  created() {
    this.isLoading = true;
    this.$store.dispatch('scrollTop');
    this.$store
      .dispatch('ticketing/loadTicket')
      .then(() => {
        const ttc = this.$store.getters['ticketing/ticketTypeCount'];
        const tt = this.$store.getters['ticketing/ticketTypes'];
        if (tt.length <= 0) {
          this.error = 'There is no available ticket type for this movie';
        } else {
          if (ttc && !tt.some(t => ttc[t.Name])) {
            ttc = {};
          }
          const { ...ticketTypeCount } = ttc || {};
          this.ticketTypeCount = ticketTypeCount;
        }
      })
      .catch(err => {
        this.error = err;
      })
      .finally(() => {
        this.isLoading = false;
      });
  },
  computed: {
    imageUrl() {
      return this.$store.getters['movies/movieImage'];
    },
    selectedMovie() {
      return this.$store.getters['movies/selectedMovie'];
    },
    ticketDetail() {
      return this.$store.getters['ticketing/ticketDetail'];
    },
    plainDateSelected() {
      return this.$store.getters['ticketing/plainDateSelected'];
    },
    ticketTypes() {
      return this.$store.getters['ticketing/ticketTypes'];
    },

    totalTicketsPrice() {
      const types = this.ticketTypes;
      const counts = this.ticketTypeCount;
      const tPrice = types.reduce((sum, type) => sum + +type.Price * (+counts[type.Name] || 0), 0);
      return tPrice;
    },
    formattedTotalTicketsPrice() {
      return this.totalTicketsPrice.toFixed(2);
    },
    totalCount: function() {
      const counts = Object.values(this.ticketTypeCount);
      return counts.reduce((sum, count) => sum + count, 0);
    },
    totalServicePrice: function() {
      const tPrice = this.totalCount * this.ticketDetail.ServiceCharge;
      return tPrice;
    },
    formattedTotalServicePrice() {
      return this.totalServicePrice.toFixed(2);
    },
    totalPrice() {
      let tPrice = this.totalTicketsPrice + this.totalServicePrice + this.totalTax;
      return tPrice.toFixed(2);
    },
    totalTax() {
      return (this.ticketDetail.tax / 100) * this.totalTicketsPrice;
    },
    formattedTotalTax() {
      return this.totalTax.toFixed(2);
    },
    formIsValidate() {
      if (this.totalCount > 0 && this.totalCount < 11) {
        return true;
      } else {
        return false;
      }
    }
  }
};
</script>

<style scoped></style>
