<script>
  export default {
    name: 'availableSeatTypes',
    props: {
      seatTypes: Array
    },
    computed: {
      hasRegularSeat() {
        return this.seatTypes && this.seatTypes.find(seatType => seatType.ID === '0')
      },
      hasReservedSeat() {
        return this.seatTypes && this.seatTypes.find(seatType => seatType.ID === '')
      },
      hasAisleSeat() {
        return this.seatTypes && this.seatTypes.find(seatType => seatType.ID === '2')
      },
      hasWheelchairPlace() {
        return this.seatTypes && this.seatTypes.find(seatType => seatType.ID === '1')
      },
      hasCompanionPlace() {
        return this.seatTypes && this.seatTypes.find(seatType => seatType.ID === '4')
      },
      hasUnavailableSeat() {
        return this.seatTypes && this.seatTypes.find(seatType => seatType.ID === '')
      }
    }
  };
</script>
