<template>
  <div id="main" aria-label="content">
    <header></header>
    <main>
      <section class="section section--steps">
        <div class="section__inner-wrapper">
          <steps :activeStep="'payment'"></steps>
        </div>
      </section>

      <section class="section section--showtimes">
        <div class="section__inner-wrapper">
          <div v-if="isProcessing" class="c-loading c-loading--movies">
            <svg
              width="100"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 100 100"
              preserveAspectRatio="xMidYMid"
              class="lds-dual-ring"
              style="background:0 0"
            >
              <circle
                cx="50"
                cy="50"
                fill="none"
                stroke-linecap="round"
                r="40"
                stroke-width="4"
                stroke="#363472"
                stroke-dasharray="62.83185307179586 62.83185307179586"
                transform="rotate(162.457 50 50)"
              >
                <animateTransform
                  attributeName="transform"
                  type="rotate"
                  calcMode="linear"
                  values="0 50 50;360 50 50"
                  keyTimes="0;1"
                  dur="1s"
                  begin="0s"
                  repeatCount="indefinite"
                />
              </circle>
            </svg>
          </div>
          <div v-else-if="ticketDetail && selectedMovie" class="c-showtimes c-showtimes--steps">
            <div class="c-showtimes__col">
              <div class="c-showtimes__steps">
                <div class="c-showtimes__steps__col c-showtimes__steps__movies">
                  <div class="c-showtimes__movies__cover">
                    <img v-lazy="imageUrl" alt="" class="c-showtimes__movies__image" />
                  </div>
                  <div class="c-showtimes__movies__info">
                    <h3 class="c-showtimes__movies__title">
                      {{ selectedMovie.Title | titleCase }}
                    </h3>
                    <div class="c-showtimes__movies__details u-flex">
                      <p class="c-showtimes__details">{{ plainDateSelected }}</p>
                      <p class="c-showtimes__details">{{ ticketDetail.cinemaName }}</p>
                      <p class="c-showtimes__details">{{ ticketDetail.salonName }}</p>
                    </div>
                  </div>
                </div>
                <div class="c-showtimes__steps__col">
                  <div class="c-payments">
                    <h1 class="section__title section__title--secondary">Your information</h1>
                    <form action="" class="c-payments__form">
                      <div class="c-payments__form__row">
                        <div class="c-payments__form__item">
                          <label for="firstname">First Name</label>
                        </div>
                        <div class="c-payments__form__item">
                          <input
                            type="text"
                            id="firstname"
                            v-model="firstName"
                            placeholder="Enter your first name"
                            class="c-payments__input required"
                          />
                        </div>
                      </div>
                      <div class="c-payments__form__row">
                        <div class="c-payments__form__item">
                          <label for="lastname">Last Name</label>
                        </div>
                        <div class="c-payments__form__item">
                          <input
                            type="text"
                            id="lastname"
                            v-model="lastName"
                            placeholder="Enter your last name"
                            class="c-payments__input required"
                          />
                        </div>
                      </div>
                      <!-- <div class="c-payments__form__row">
                        <div class="c-payments__form__item">
                          <label for="delivery">Receipt & Ticket Delivery</label>
                        </div>
                        <div class="c-payments__form__item">
                          <input
                            type="email"
                            id="delivery"
                            v-model="email"
                            placeholder="Enter your email address"
                            class="c-payments__input required"
                          />
                          <div class="validation-error" v-if="!isEmailValid">
                            Valid email required.
                          </div>
                        </div>
                      </div> -->
                      <div class="c-payments__form__row">
                        <div class="c-payments__form__item">
                          <label for="delivery">Receipt & Ticket Delivery</label>
                        </div>
                        <div class="c-payments__form__item">
                          <email-input :inputId="'delivery'" @emailChanged="onEmailChange" />
                          <div class="validation-error" v-if="!isEmailValid">
                            Valid email required.
                          </div>
                        </div>
                      </div>
                    </form>
                    <div class="c-giftcard">
                      <div
                        class="c-giftcard__title"
                        :class="collapseVisible ? null : 'is-collapsed'"
                        :aria-expanded="collapseVisible ? 'true' : 'false'"
                        aria-controls="collapse_giftcard"
                        @click="collapseVisible = !collapseVisible"
                      >
                        I have a Gift Card
                      </div>
                      <b-collapse
                        id="collapse_giftcard"
                        v-model="collapseVisible"
                        class="c-giftcard__collapse"
                      >
                        <form
                          @submit.prevent="getGiftCardBalance"
                          action=""
                          class="c-giftcard__form"
                        >
                          <div class="c-giftcard__wrap">
                            <!-- if giftcard has error add ' has-error ' class to c-giftcard__items-->
                            <div class="c-giftcard__items">
                              <div class="c-giftcard__col">
                                <label for="giftcard__num">Card Number</label>
                              </div>
                              <div
                                class="c-giftcard__col"
                                :class="
                                  triedGiftCard
                                    ? giftCardIsValid
                                      ? 'has-giftcard'
                                      : 'has-error'
                                    : ''
                                "
                              >
                                <input
                                  pattern="[0-9]+"
                                  class="c-giftcard__input"
                                  v-model="giftCardNumber"
                                  type="number"
                                  id="giftcard__num"
                                  placeholder="Enter your gift card number"
                                />

                                <div class="c-giftcard__message" v-if="giftCardError">
                                  <div class="c-giftcard__status">
                                    <svg
                                      width="16"
                                      viewBox="0 0 16 16"
                                      fill="none"
                                      xmlns="http://www.w3.org/2000/svg"
                                    >
                                      <path
                                        d="M8 0a8 8 0 108 8 8.024 8.024 0 00-8-8zm3.3 10.357l-.943.943L8 8.943 5.643 11.3l-.943-.943L7.057 8 4.7 5.643l.943-.943L8 7.057 10.357 4.7l.943.943L8.943 8l2.357 2.357z"
                                        fill="#DF2137"
                                      />
                                    </svg>
                                  </div>
                                  <div class="c-giftcart_message">{{ giftCardError }}</div>
                                </div>
                                <div class="c-giftcard__message" v-if="giftCardIsValid">
                                  <div class="c-giftcard__status">
                                    <svg
                                      width="16"
                                      viewBox="0 0 16 16"
                                      fill="none"
                                      xmlns="http://www.w3.org/2000/svg"
                                    >
                                      <path
                                        d="M8 0C3.6 0 0 3.6 0 8s3.6 8 8 8 8-3.6 8-8-3.6-8-8-8zM7 11.4L3.6 8 5 6.6l2 2 4-4L12.4 6 7 11.4z"
                                        fill="#1CC923"
                                      />
                                    </svg>
                                  </div>
                                </div>
                              </div>
                            </div>
                            <div class="c-giftcard__items">
                              <div class="c-giftcard__col">
