                  <h3 class="c-info__title c-info--title-summary">purchase summary</h3>
                  <ul class="c-info__summary__list">
                    <li class="c-info__summary__list__items">
                      <div class="c-info__summary__text">{{ totalTicketCount }} Ticket</div>
                      <div class="c-info__summary__text">${{ totalTicketsPrice }}</div>
                    </li>
                    <li class="c-info__summary__list__items is-details">
