                          :class="ticketVisible ? null : 'is-collapsed'"
                          :aria-expanded="ticketVisible ? 'true' : 'false'"
                          aria-controls="ticket_details_list"
                          @click="ticketVisible = !ticketVisible"
                        >
                          Ticket Details
                        </div>
                        <b-collapse
                          id="ticket_details_list"
                          v-model="ticketVisible"
                          class="c-giftcard__collapse"
                        >
                          <div v-for="entry in Object.entries(ticketTypeCount)">
                            <span>
                              {{ entry[0] }}
                            </span>
                            <span>
                              x
                              {{ entry[1] }} = ($
                              {{
                                ticketTypes.find(type => type.Name === entry[0]).Price * entry[1]
                              }}
                              )
                            </span>
                          </div>
                        </b-collapse>
                      </div>
                    </li>
                    <li class="c-info__summary__list__items">
                      <div class="c-info__summary__text">Service Charge</div>
                      <div class="c-info__summary__text">${{ totalServicePrice }}</div>
                    </li>
