                      >
                        <path
                          d="M9 0a9 9 0 109 9 9.026 9.026 0 00-9-9zm3.713 11.652l-1.061 1.06L9 10.06l-2.652 2.652-1.06-1.06L7.938 9 5.287 6.348l1.061-1.06L9 7.938l2.652-2.652 1.06 1.061L10.06 9l2.652 2.652z"
                          fill="#DF2137"
                        />
                      </svg>
                      <span>Error:</span> {{ errorMessage }}
                    </div>
                  </div>
                </div>
                <stop-watch></stop-watch>
              </div>
            </div>
          </div>
        </div>
      </section>
    </main>
    <footer></footer>
  </div>
</template>

<script>
import Steps from '@/components/steps';
import StopWatch from '@/components/stop-watch';
import CreditCardField from 'vue-credit-card-field/src/Components/CreditCardField';
import EmailInput from '../../components/email-input.vue';

export default {
  name: 'payment',
  components: {
    CreditCardField,
    StopWatch,
    Steps,
    EmailInput
  },
  props: {
    title: String
  },
  data() {
    const paymentData = this.$store.getters['ticketing/paymentData'] || {};
    const giftCardNumber = this.$store.getters['ticketing/giftCardNumber'];
    const giftCardPin = this.$store.getters['ticketing/giftCardPin'];
    const giftCardBalance = this.$store.getters['ticketing/giftCardBalance'];
    const {
      firstName = '',
      lastName = '',
      email = '',
      method: isUsingCreditCard = true
    } = paymentData;
    let card = {};
    let cardDefaultValue = { brand: '', number: '', expMonth: '', expYear: '' };
    let cardValidation = false;
    if (isUsingCreditCard && paymentData.cardBrand) {
      const {
        cardBrand: brand,
        cardNumber: number,
        expirationMonth: expMonth,
        expirationYear: expYear
      } = paymentData;
      cardValidation = true;
      cardDefaultValue = { brand, number, expMonth, expYear };
    }
    return {
      card,
      cardDefaultValue,
      isUsingCreditCard,
      cardValidation,
      giftCardPin: giftCardPin,
      giftCardNumber: giftCardNumber,
      giftCardBalance: giftCardBalance,
      triedGiftCard: !!giftCardNumber,
      loadingGiftCard: false,
      giftCardError: '',
      errorMessage: '',
      firstName,
      lastName,
      email,
      isProcessing: false,
      collapseVisible: !!giftCardNumber,
      ticketVisible: false
    };
  },
  created() {
    const paymentData = this.$store.getters['ticketing/paymentData'] || {};
    this.setPaymentData(paymentData);
    this.$store.dispatch('scrollTop');
  },
  computed: {
    imageUrl() {
      return this.$store.getters['movies/movieImage'];
    },
    isEmailValid() {
      const re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
      return !this.email || re.test(this.email);
    },
    paymentData() {
      return this.$store.getters['ticketing/paymentData'];
    },
    selectedMovie() {
      return this.$store.getters['movies/selectedMovie'];
    },
    ticketDetail() {
      return this.$store.getters['ticketing/ticketDetail'];
    },
    plainDateSelected() {
      return this.$store.getters['ticketing/plainDateSelected'];
    },
    totalTicketCount() {
      return this.$store.getters['ticketing/totalTicketCount'];
    },
    ticketTypes() {
      const types = JSON.parse(JSON.stringify(this.$store.getters['ticketing/ticketTypes']));

      return [...types];
    },

    ticketTypeCount() {
      return this.$store.getters['ticketing/ticketTypeCount'];
    },
    isFormValid: function() {
      const {
        firstName,
        lastName,
        email,
        isEmailValid,
        cardValidation,
        giftCardNumber,
        giftCardBalance,
        isUsingCreditCard
      } = this;
      return (
        firstName &&
        lastName &&
        email &&
        isEmailValid /*&& (isUsingCreditCard ? cardValidation : giftCardNumber && giftCardBalance)*/
      );
    },
    totalTicketsPrice() {
      return this.$store.getters['ticketing/totalTicketsPrice'];
    },
    totalServicePrice() {
      return this.$store.getters['ticketing/totalServicePrice'];
    },
    totalPrice() {
      return this.$store.getters['ticketing/totalPrice'];
    },
    giftCardIsValid() {
      return this.triedGiftCard && this.giftCardBalance;
    },
    maxUsingGiftCard() {
      return this.$store.getters['ticketing/giftCardMaxUsage'].toFixed(2);
    },
    priceGroups() {
      const groups = [...JSON.parse(JSON.stringify(this.$store.getters['ticketing/priceGroups']))];
      return groups;
    }
  },
  watch: {
    paymentData(paymentData) {
      this.setPaymentData(paymentData);
    }
  },
  methods: {
    setPaymentData(paymentData) {
      const {
        firstName = '',
        lastName = '',
        email = '',
        method: isUsingCreditCard = true
      } = paymentData;
      let card = { brand: '', number: '', expMonth: '', expYear: '', cvc: '' };
      let cardValidation = false;
      if (isUsingCreditCard && paymentData.cardBrand) {
        const {
          cardBrand: brand,
          cardNumber: number,
          cvc,
          expirationMonth: expMonth,
          expirationYear: expYear
        } = paymentData;
        cardValidation = true;
        card = { brand, number, expMonth, expYear, cvc };
      }
      this.firstName = firstName;
      this.lastName = lastName;
      this.email = email;
      this.isUsingCreditCard = isUsingCreditCard;
      this.cardDefaultValue = card;
      this.card = card;
    },
    onChange() {
      console.log('change');
      console.log(this.card);
    },
    onEmailChange(e) {
      this.email = e;
    },
    onValid(e) {
      console.log('valid ***********');
      this.cardValidation = true;
    },
    onInvalid() {
      console.log('invalid');
      this.cardValidation = false;
    },
    getGiftCardBalance() {
      this.giftCardError = '';
      this.loadingGiftCard = true;
      this.$store
        .dispatch('ticketing/getGiftCardDetail', {
          giftCardNumber: this.giftCardNumber,
          giftCardPin: this.giftCardPin
        })
        .then(({ DebitRemain, Error }) => {
          this.loadingGiftCard = false;
          this.triedGiftCard = true;
          if (Error) {
            this.giftCardError = Error;
            this.giftCardBalance = 0;
          } else {
            this.giftCardError = '';
            this.giftCardBalance = DebitRemain;
          }
        });
    },
    resetGiftCard() {
      this.giftCardNumber = '';
      this.giftCardPin = '';
      this.giftCardError = '';
      this.giftCardBalance = 0;
      this.triedGiftCard = false;
      this.$store.commit('ticketing/giftcard', {
        giftCardBalance: 0,
        giftCardNumber: 0,
        giftCardPin: 0
      });
    },
    goToReview() {
      if (this.isFormValid) {
        this.isProcessing = true;
        const method = this.isUsingCreditCard ? 'Credit Card' : 'Gift Card';

        if (this.isUsingCreditCard) {
          this.$store.dispatch('ticketing/setPaymentData', {
            method,
            firstName: this.firstName,
            lastName: this.lastName,
            cardBrand: this.card.brand,
            // cvc: this.card.cvc,
            // cardNumber: this.card.number,
            // expirationMonth: this.card.expMonth,
            // expirationYear: this.card.expYear,
            email: this.email
          });
        } else {
          this.$store.dispatch('ticketing/setPaymentData', {
            method,
            firstName: this.firstName,
            lastName: this.lastName,
            email: this.email,
            giftCardNumber: this.giftCardNumber,
            giftCardBalance: this.giftCardBalance
          });
        }
        this.$store
          .dispatch('ticketing/sendLogData', this.selectedMovie)
          .then(res => {
            const resp = res.body.Response;
            const { Version, ErrorText, Packet } = resp.Response || resp;
            let { Worked, RedirectUrl, Order, PostData } =
              Packet && Packet.CreatePayment ? Packet.CreatePayment : {};
            if (RedirectUrl) {
              this.$store.commit('ticketing/redirectUrl', {
                RedirectUrl: decodeURIComponent(RedirectUrl),
                Order
              });
            }
            if (Version === '1' && Worked === '1') {
              this.$store.commit('ticketing/logID', PostData);
              this.$router.push({ name: 'review', params: { title: this.$route.params.title } });
            } else if (ErrorText) {
              this.errorMessage = ErrorText;
            }
            this.isProcessing = false;
          })
          .catch(error => {
            this.errorMessage = error;
          });
      }
      // brand: "visa"
      // cvc: "334"
      // expMonth: "05"
      // expYear: "2025"
      // expiration: "05 / 2025"
      // number: "4242424242424242"
      // numberFormatted: "4242 4242 4242 4242"
    }
  }
};
</script>

<style scoped>
.credit-card-field {
  width: 305px;
}
</style>
