<template>
  <div>
    <input
      @keyup="handleEvent"
      @keypress="handleEvent"
      :id="inputId"
      class="c-payments__input required"
      placeholder="Enter your email address"
      :value="this.bindTo"
    />
    <span class="suggestion" v-if="this.suggestedEmail.length > 0" @click="applySuggestion">
      <p>Did you mean "{{ this.suggestedEmail }}"?</p>
    </span>
  </div>
</template>

<script>
export default {
  name: 'email-input',
  props: {
    inputId: {
      type: String,
      required: false,
      default: 'email'
    }
  },
  methods: {
    isValidEmailAddress: function(emailAddress) {
      var pattern = /^([a-z\d!#$%&'*+\-\/=?^_`{|}~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]+(\.[a-z\d!#$%&'*+\-\/=?^_`{|}~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]+)*|"((([ \t]*\r\n)?[ \t]+)?([\x01-\x08\x0b\x0c\x0e-\x1f\x7f\x21\x23-\x5b\x5d-\x7e\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]|\\[\x01-\x09\x0b\x0c\x0d-\x7f\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]))*(([ \t]*\r\n)?[ \t]+)?")@(([a-z\d\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]|[a-z\d\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF][a-z\d\-._~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]*[a-z\d\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])\.)+([a-z\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]|[a-z\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF][a-z\d\-._~\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF]*[a-z\u00A0-\uD7FF\uF900-\uFDCF\uFDF0-\uFFEF])\.?$/i;
      return pattern.test(emailAddress);
    },
    checkForTypo: function(email) {
      return this.suggest(email.trim().toLowerCase());
    },

    suggest: function(email) {
      const [lPart, rPart] = email.split('@');
      const [rPart1, rPart2] = rPart.split('.');

      var rPart1Arr = rPart1.split('');

      var matched = [];

      this.domains.forEach(domain => {
        var domainArr = domain.split('');

        var intersection1 = rPart1Arr.filter(x => !domainArr.includes(x));
        var intersection2 = domainArr.filter(x => !rPart1Arr.includes(x));
        if (
          (intersection1.length <= this.accuracy && intersection2.length <= this.accuracy) ||
          (intersection1.length == 0 && intersection2.length <= this.accuracy + 1)
        ) {
          matched.push(lPart + '@' + domain + '.com');
        }
      });

      return matched;
    },

    handleSuggestions: function(suggestions) {
      if (suggestions.length == 0) {
        return '';
      } else {
        return suggestions[0];
      }
    },
    handleEvent: function(e) {
      this.bindTo = e.target.value;
      this.$emit('emailChanged', this.bindTo);
      this.setMistakeEmailsMessage(e);
    },
    setMistakeEmailsMessage: function(e) {
      //check if input is a email.
      if (this.isValidEmailAddress(e.target.value)) {
        // check typo of entered email.
        var suggestion = this.checkForTypo(e.target.value);
        var suggestedEmailhandled = this.handleSuggestions(suggestion);

        if (suggestion.length > 0 && !suggestion.includes(e.target.value.toLowerCase())) {
          this.suggestedEmail = suggestedEmailhandled;
        } else {
          this.suggestedEmail = '';
          // $('.validate-mistake-emails-message:eq(' + i + ')')
          //   .html('')
          //   .hide();
        }
      } else {
        this.suggestedEmail = '';
        // $('.validate-mistake-emails-message:eq(' + i + ')')
        //   .html('')
        //   .hide();
      }
    },
    applySuggestion: function() {
      if (this.suggestedEmail.length > 0) {
        this.bindTo = this.suggestedEmail;
        this.suggestedEmail = '';
        this.$emit('emailChanged', this.bindTo);
      }
    }
  },
  data() {
    return {
      domains: ['aol', 'zoho', 'gmail', 'live', 'icloud', 'mail', 'yahoo', 'hotmail', 'outlook'],
      accuracy: 1,
      suggestedEmail: '',
      bindTo: ''
    };
  }
};
</script>
