<script>
import AvailSeatsTypes from '@/components/available-seat-types';
import Steps from '@/components/steps';
import StopWatch from '@/components/stop-watch';

//export let ticketDetail = [];

export default {
  name: 'seats',
  components: {
    StopWatch,
    Steps,
    AvailSeatsTypes
  },
  props: {
    title: String
  },
  data() {
    const selectedList = this.$store.getters['ticketing/selectedSeats'] || [];
    const selectedSeats = selectedList.reduce((obj, item) => {
      obj[item.id] = item;
      return obj;
    }, {});
    return {
      isHolding: false,
      holdError: '',
      selectedSeats,
      modalMessage: '',
      activeSeat: null,
      ticketTypeCount: {},
      activeSelectType: null
    };
  },
  created() {
    this.$store.dispatch('scrollTop');
    this.$store.dispatch('ticketing/loadTicket').then(this.checkSelectedSeats);
    this.reloadInterval = setInterval(() => {
      if (!this.holdId) {
        this.$store.dispatch('ticketing/loadTicket').then(this.checkSelectedSeats);
      }
    }, 30 * 1000);
  },
  destroyed() {
    clearInterval(this.reloadInterval);
  },

  methods: {
    checkSelectedSeats() {
      this.seatsList.forEach(seat => {
        if (this.selectedSeats[seat.id] && seat.Status != 0) {
          delete this.selectedSeats[seat.id];
        }
      });
      this.selectedSeats = { ...this.selectedSeats };
    },
    closeWarning() {
      this.$modal.hide('seat-warning');
    },
    closeTicketType() {
      this.$modal.hide('ticket-type');
    },
    toggleSeat(seat) {
      this.activeSeat = null;
      if (this.selectedSeats[seat.id] || seat.Status < 1) {
        // debugger;
        if (this.selectedSeats[seat.id]) {
          // this.selectedSeats[seat.id] = null;
          delete this.selectedSeats[seat.id];

          this.selectedSeats = { ...this.selectedSeats };

          this.holdError = '';
          if (this.holdTransactionId) {
            this.$store.dispatch('ticketing/resetCountDownReleaseSeat');
          }
        } else if (
          (this.checkPriceGroups && this.totalTicketCount < 10) ||
          this.selectedSeatsList.length < this.totalTicketCount
        ) {
          this.activeSeat = seat;

          if (this.checkPriceGroups) {
            this.activeSelectType = this.ticketTypes.find(
              avTick =>
                avTick.Code ===
                this.priceGroups.find(group => group.Code === seat.PriceGroupCode).Tickets.Ticket
                  .Code
            ).Name;
          }

          this.checkSeatType();
          this.setSeatType();
          //here

          // this.$modal.show('ticket-type');
        }
      }
    },
    checkSeatType() {
      if (this.activeSeat.Type === '1') {
        this.modalMessage = 'You have selected a wheelchair space, not a seat';
        this.$modal.show('seat-warning');
      } else if (this.activeSeat.Type === '4') {
        this.modalMessage =
          'You have selected a seat reserved for a companion of a guest using a wheelchair. If you do not have a disability requiring use of this chair or accompanying a guest in a wheelchair, then you will be asked to relocate to a different seat (if available; we cannot guarantee availability of another seat).';
        this.$modal.show('seat-warning');
      }
    },
    setSeatType() {
      // if (!this.activeSelectType) return;
      this.selectedSeats[this.activeSeat.id] = this.activeSeat;

      this.selectedSeats = { ...this.selectedSeats };

      this.holdError = '';
      if (this.holdTransactionId) {
        this.$store.dispatch('ticketing/resetCountDownReleaseSeat');
      }

      if (this.checkPriceGroups) {
        if (this.ticketTypeCount[this.activeSelectType] === undefined) {
          this.ticketTypeCount[this.activeSelectType] = 0;
        }

        if (this.totalTicketCount <= 10) {
          this.ticketTypeCount[this.activeSelectType]++;
          this.ticketTypeCount = { ...this.ticketTypeCount };
        }

        this.$store.dispatch('ticketing/setTicketCount', {
          ticketTypeCount: this.ticketTypeCount
        });

        this.activeSelectType = null;
      }
      // this.closeTicketType();
    },
    async submit() {
      if (!this.formIsValid) {
        return;
      }
      this.isHolding = true;
      let holdTransactionId = this.holdTransactionId;
      if (!holdTransactionId) {
        try {
          const resp = await this.$store.dispatch('ticketing/holdSeats', this.selectedSeatsList);
          if (resp.Error) {
            this.holdError = resp.Error;
            this.selectedSeats = {};
            this.$store.dispatch('ticketing/loadTicket').then(this.checkSelectedSeats);
          } else if (resp.TransactionID) {
            holdTransactionId = resp.TransactionID;
          }
        } catch (e) {}
      }
      if (holdTransactionId) {
        this.$store.dispatch('ticketing/setSelectedSeats', this.selectedSeatsList);

        this.$router.push({ name: 'payment', params: { title: this.selectedMovie.Slug } });
      }
      this.isHolding = false;
    }
  },
  computed: {
    holdTransactionId() {
      return this.$store.getters['ticketing/holdId'];
    },
    avalSeatTypes() {
      const { seatTypes } = this.$store.getters['ticketing/seats'];
      return seatTypes;
    },
    ticketTypes() {
      return this.$store.getters['ticketing/ticketTypes'];
    },
    // totalTicketCount() {
    //   return Object.keys(this.selectedSeats).length;
    // },
    seatsList() {
      const { seats, gridWidth, gridHeight } = this.$store.getters['ticketing/seats'];
      const selectedSeats = (this.$store.getters['ticketing/selectedSeats'] || []).reduce(
        (obj, seat) => {
          obj[seat.id] = seat;
          return obj;
        },
        {}
      );
      if (!seats) {
        return [];
      }

      return seats
        .filter(s => s)
        .map(
          ({
            GroupID,
            ColNum,
            RowNum,
            RowDes,
            ColDes,
            XPos,
            YPos,
            Width,
            Height,
            Status,
            Type,
            Section,
            PriceGroupCode
          }) => {
            const margin = 0;
            const nx = +XPos / gridWidth - margin;
            const ny = +YPos / gridHeight - margin;
            const nWidth = +Width / gridHeight;
            const nHeight = +Height / gridHeight;
            const winWidth = 600;
            const id = XPos + ' ' + YPos;

            let seatData = {
              Type,
              Status: selectedSeats[id] ? 0 : Status
            };

            if (this.checkPriceGroups && RowDes === 'D') {
              if (ColDes == 2 || ColDes == 5 || ColDes == 6 || ColDes == 9) {
                seatData.Type = '4';
              } else {
                seatData.Type = '1';
              }
            }

            const seat = {
              x: nx * 100,
              y: ny * 100,
              width: 35,
              height: 30,
              desc: RowDes + ColDes,
              Section,
              id,
              fill: '#003F8776',
              ColNum,
              RowNum,
              PriceGroupCode,
              ...seatData
            };

            return seat;
          }
        );
    },
    selectedSeatsList() {
      const list = [...JSON.parse(JSON.stringify(Object.values(this.selectedSeats)))];
      return list;
    },
    yourSelectedSeats() {
      return this.selectedSeatsList.map(s => s.desc).join(' , ');
    },
    imageUrl() {
      return this.$store.getters['movies/movieImage'];
    },
    selectedMovie() {
      return this.$store.getters['movies/selectedMovie'];
    },
    ticketDetail() {
      return this.$store.getters['ticketing/ticketDetail'];
    },

    priceGroups() {
      const groups = [...JSON.parse(JSON.stringify(this.$store.getters['ticketing/priceGroups']))];

      return groups;
    },
    plainDateSelected() {
      return this.$store.getters['ticketing/plainDateSelected'];
    },
    totalTicketCount() {
      if (this.checkPriceGroups) {
        return Object.keys(this.selectedSeats).length;
      }

      return this.$store.getters['ticketing/totalTicketCount'];
    },
    checkPriceGroups() {
      return this.priceGroups.length > 1;
    },
    formIsValid: function() {
      if (this.checkPriceGroups) {
        return this.totalTicketCount > 0;
      }

      return this.totalTicketCount === this.selectedSeatsList.length && this.totalTicketCount > 0;
    }
  }
};
</script>

